import React, { useCallback, useState, useEffect } from "react";
import {loadStripe} from '@stripe/stripe-js';
import {
  EmbeddedCheckoutProvider,
  EmbeddedCheckout
} from '@stripe/react-stripe-js';
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate, useSearchParams
} from "react-router-dom";


const CheckoutForm = () => {
  const [clientSecret, setClientSecret] = useState(null);
  const [publishableKey, setPublishableKey] = useState(null);
  const [launched, setLaunched] = useState(false);

  const fetchClientSecret = useCallback(() => {
    // Create a Checkout Session
    return "cs_test_a1MZO1tOmrwdtyTKGz3ADUinQmL5SX3aLxzrnZUIOOX7ksmm1L2u7GdpKU_secret_fidwbEhqYWAnPydgaGdgYWFgYScpJ2lkfGpwcVF8dWAnPyd2bGtiaWBabHFgaCcpJ3dgYWx3YGZxSmtGamh1aWBxbGprJz8na2BzYHcneCUl"
  }, []);

  const Checkout = (key, secret) => {
    const stripePromise = loadStripe(key);
    const options = {
      clientSecret: secret,
      onComplete: () => console.log("Success")
    };
    return <div id="checkout">
      <EmbeddedCheckoutProvider
        stripe={stripePromise}
        options={options}
      >
        <EmbeddedCheckout/>
      </EmbeddedCheckoutProvider>
    </div>

  }

  return <>
    <div>
      <input placeholder={"Publishable Key"} type={"text"} onChange={(e) => {
        setPublishableKey(e.target.value)
      }}/>
      <br/>
      <input placeholder={"Client Secret"} type={"text"} onChange={(e) => { setClientSecret(e.target.value) }}/>
      <br />
      <button onClick={() => { setLaunched(prevState => !prevState) }}>Launch</button>
    </div>
    {launched && Checkout(publishableKey, clientSecret)}
  </>
}

const App = () => {
  return (
    <div className="App">
      <Router>
        <Routes>
          <Route path="/" element={<CheckoutForm />} />
        </Routes>
      </Router>
    </div>
  )
}

export default App;